/*
* @subsection   Isotope
*/

$isotope-masonry-item-offset: 30px;
html .page {

  [data-isotope-layout] {
    position: relative;
    transform: translateY(-$isotope-masonry-item-offset);
    display: block;
    margin-bottom: -$isotope-masonry-item-offset;
    transition: .4s all ease;
    min-height: 160px;

    &:after {
      content: '';
      position: absolute;
      margin-top: $isotope-masonry-item-offset / 2;
      width: 64px;
      height: 64px;
      top: 50%;
      left: 50%;
      background-image: url('data:image/png;base64,');
      background-position: -1152px 0;
      animation: 0.7s sprite-animation steps(18) infinite;
      transition: .4s all ease;
      transform: translate(-50%, -50%);
    }

    [class*="col-"] {
      display: block;
      margin-top: $isotope-masonry-item-offset;
      opacity: 0;
      transition: .4s opacity ease;
      /*
       * @bugfix item position after filtering
       * @affected iOS
      */
      //width: 100%;
    }

    &.isotope--loaded {
      [class*="col-"] {
        opacity: 1;
      }

      &:after {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  .isotope-filters {
    margin-bottom: 41px;
    margin-top: 57px;
    > * {
      margin-top: 0;
      display: inline-block;
      vertical-align: middle;
    }

    .list-inline {
      > li {
        > a {
          color: $body-color;
          &:hover,
          &.active {
            color: $primary;
          }
        }
      }
    }

    @include media-breakpoint-up(lg) {
      .isotope-filters-trigger {
        display: none;
      }
    }
  }

}
